@import './spritesheets.scss';
@import './functions.scss';

.ch-sprite {
    @include sprite-builder(
        map-get($characters, "prom"), 
        var(--character-sprites), 
        0.1, 
        "ch"
    );
}

.ui-sprite {
    @include sprite-builder(
    $ui, 
    var(--img), 
    0.25, 
    "ui"
  );
}

.st-sprite {
    @include sprite-builder(
        $stats, 
        var(--stat-sprites), 
        0.5,
        "st",
        ("fun", "charming")
    );
}
